import { useState } from 'react';

interface Modal {
  isShowing: boolean;
  toggle(): void;
}

const useModal = (): Modal => {
  const [isShowing, setIsShowing] = useState(false);

  function toggle() {
    setIsShowing(state => !state);
  }

  return {
    isShowing,
    toggle,
  };
};

export default useModal;
