import React from 'react';
import ReactDOM from 'react-dom';

import { ReactComponent as CloseIcon } from 'assets/svg/close_icon.svg';

import Button from 'components/Button';

import { ModalProps } from './props';

import {
  ModalOverlay,
  Modal as Container,
  ModalHeader,
  Title,
  CloseButton,
  ModalBody,
  ModalFooter,
  ModalFooterContent,
  ModalButtons,
} from './styles';

const Modal: React.FC<ModalProps> = ({
  children,
  isShowing,
  hide,
  title,
  footer,
  hideCloseButton = false,
  hideOkButton = false,
  handleOk,
  handleClose,
  okText = 'Ok',
  closeText = 'Cancelar',
  buttonSize = 'sm',
}) => {
  const modal = (
    <ModalOverlay>
      <Container>
        <ModalHeader>
          <Title>{title}</Title>
          <CloseButton onClick={hide}>
            <CloseIcon />
          </CloseButton>
        </ModalHeader>

        <ModalBody>{children}</ModalBody>

        <ModalFooter>
          {footer && <ModalFooterContent>{footer}</ModalFooterContent>}

          {(!hideOkButton || !hideCloseButton) && (
            <ModalButtons>
              {!hideCloseButton && (
                <Button
                  text={closeText}
                  onClick={handleClose || hide}
                  size="md"
                />
              )}
              {!hideOkButton && (
                <Button text={okText} onClick={handleOk} size={buttonSize} />
              )}
            </ModalButtons>
          )}
        </ModalFooter>
      </Container>
    </ModalOverlay>
  );

  return isShowing ? ReactDOM.createPortal(modal, document.body) : null;
};

export default Modal;
