import React from 'react';
import { useHistory } from 'react-router-dom';

import Button from 'components/Button';
import Form from 'components/Form';
import Field from 'components/Form/Field';
import Select from 'components/Form/Select';
import Modal from 'components/Modal';
import TextArea from 'components/TextArea';

import { DateHours, FieldWithLink, ButtonsContainer } from './styles';

interface AppointmentFormProps {
  isShowing: boolean;
  toggle(): void;
  initialValues?: object;
}
const AppointmentForm: React.FC<AppointmentFormProps> = ({
  isShowing,
  toggle,
  initialValues,
}) => {
  const history = useHistory();

  const handleClickAddClient = () => {
    history.push({
      pathname: '',
      search: '?openPartner=true',
    });
  };

  const modalTitle = () => {
    return (
      <div>
        NOVO
        <strong> AGENDAMENTO</strong>
      </div>
    );
  };

  const options = [
    { value: 0, label: 'Chocolateeeeeeeeeee' },
    { value: 1, label: 'Strawberry' },
    { value: 2, label: 'Vanilla' },
  ];

  return (
    <Modal
      isShowing={isShowing}
      hide={toggle}
      title={modalTitle()}
      hideOkButton
      hideCloseButton
    >
      <Form
        onSubmit={values => console.log(values)}
        initialValues={initialValues}
      >
        <DateHours>
          <Field id="data" name="data" type="date" label="Data" />
          <Field
            id="starTime"
            name="starTime"
            type="time"
            label="Hora inicio"
          />
          <Field
            id="finishTime"
            name="finishTime"
            type="time"
            label="Hora fim"
          />
        </DateHours>

        <Field
          id="checkbox"
          name="checkbox"
          type="checkbox"
          label="Bloquear agendamento nesse horário"
          contrastBgColor="blue"
        />
        <Field id="profissional" name="profissional" label="Profissional" />
        <Field id="procedimento" name="procedimento" label="Procedimento" />
        <Field id="unidade" name="unidade" label="Unidade" />
        <Field id="sala" name="sala" label="Sala" />
        <TextArea id="textarea" name="textarea" placeholder="Observações" />

        <Select id="selectt" name="selectt" options={options} searchable />

        <FieldWithLink>
          <Field id="cliente" name="cliente" label="Cliente" />
          <button type="button" onClick={handleClickAddClient}>
            + ADICIONAR
          </button>
        </FieldWithLink>

        <ButtonsContainer>
          <Button type="submit" text="Salvar" size="md" />
        </ButtonsContainer>
      </Form>
    </Modal>
  );
};

export default AppointmentForm;
