import styled, { keyframes } from 'styled-components';
import colors from '../../utils/styles/colors';
import { centerMiddle } from '../../utils/styles/commonStyles';

const modalAnimation = keyframes`
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
`;

export const ModalOverlay = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 100vh;
  max-height: 100vh;
  padding: 2%;
  background-color: rgba(0, 0, 0, 0.55);
  z-index: 1040;
  overflow: auto;
`;

export const Modal = styled.div`
  margin: auto;
  width: 90%;
  min-height: 200px;
  max-width: 420px;
  padding: 24px;
  background: white;
  animation: ${modalAnimation} 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
`;

export const ModalHeader = styled.div`
  position: sticky;
  top: -12px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  z-index: 999;

  &::before {
    content: '';
    position: absolute;
    top: -24px;
    left: 0;
    width: 100%;
    height: 24px;
    background-color: #fff;
  }
`;

export const Title = styled.div`
  font-size: 16px;
  color: ${colors.accent};
  flex: 1;
`;

export const CloseButton = styled.button`
  ${centerMiddle}
  width: 32px;
  height: 32px;
  border: 0;
  background: none;
  outline-color: ${colors.primary};
`;

export const ModalBody = styled.div`
  margin-top: 16px;
`;

export const ModalFooter = styled.div`
  display: flex;
`;

export const ModalFooterContent = styled.div`
  display: flex;
  flex: 1;
  margin-top: 32px;
`;

export const ModalButtons = styled.div`
  display: flex;
  justify-content: flex-end;
  flex: 1;
  margin-top: 32px;

  button + button {
    margin-left: 8px;
  }
`;
