import styled, { css } from 'styled-components';
import { shade } from 'polished';

import colors from '../../utils/styles/colors';
import { centerMiddleColumns } from '../../utils/styles/commonStyles';

interface AppointmentProps {
  startHour: number;
  duration: number;
  color: string;
}

export const Container = styled.div`
  position: relative;
  color: ${colors.black};
  padding-bottom: 32px;
  overflow: auto;
  flex: 1;
`;

export const Labels = styled.div`
  position: sticky;
  left: 0;
  margin: 0 24px 0 0;
  min-width: 130px;
  background-color: #fff;
  z-index: 999;
`;

export const Label = styled.div`
  ${centerMiddleColumns}
  width: 100%;
  height: 70px;
  border: 1px solid #efefef;
  font-size: 13px;

  div {
    margin-top: 2px;
    color: ${colors.black_alt};
  }

  & + div {
    margin-top: 4px;
  }
`;

export const Appointments = styled.div`
  display: flex;
  flex-direction: column;
  padding-right: 8px;
`;

export const Hours = styled.div`
  display: flex;
  margin: 0 0 32px 153px;
`;

export const Hour = styled.div`
  min-width: 120px;
  font-weight: 400;
  font-size: 13px;
`;

export const Data = styled.div`
  display: flex;
  width: 2880px;
`;

export const AppointmentsLine = styled.div`
  position: relative;
  display: flex;
  height: 70px;
  z-index: 2;

  & + div {
    margin-top: 4px;
  }

  & + div::before {
    content: '';
    position: absolute;
    top: -2px;
    width: 100%;
    height: 1px;
    background-color: #efefef;
  }
`;

export const AppointmentHour = styled.div`
  display: flex;
  align-items: center;
  width: 120px;
  padding: 4px;

  &:hover {
    border: 1px solid ${colors.light_grey};
    border-radius: 2px;
  }
`;

export const Appointment = styled.div<AppointmentProps>`
  position: absolute;
  height: 70px;
  padding: 8px;
  width: ${props => props.duration}px;
  left: ${props => props.startHour}px;
  white-space: nowrap;
  overflow: hidden;
  transition: all 0.25s;

  &:hover {
    min-width: min-content;
    transform: scale(1.025);
  }

  ${({ color }) => css`
    background-color: ${color};
    border-left: 2px solid ${shade(0.1, color)};
  `}
`;

export const AppointmentTitle = styled.span`
  font-size: 11px;
  color: ${colors.dark_grey};
`;

export const AppointmentDescription = styled.div`
  font-size: 13px;
  font-weight: 400;
  color: ${colors.black_alt};
  margin-top: 6px;
`;
