import React from 'react';

import { ButtonProps } from './props';

import { Button as ButtonElem } from './styles';

export const Button: React.FC<ButtonProps> = ({
  type = 'button',
  text,
  loading,
  loadingText = 'Loading',
  size = 'sm',
  display = 'inline-block',
  ...rest
}) => {
  return (
    <ButtonElem type={type} {...rest} size={size} display={display}>
      {loading ? loadingText : text}
    </ButtonElem>
  );
};

export default Button;
