import styled from 'styled-components';
import colors from '../../utils/styles/colors';

export const Container = styled.div`
  position: relative;

  & + div {
    margin-top: 16px;
  }

  svg {
    width: 16px;
    cursor: pointer;
  }
`;

export const Label = styled.label`
  display: block;
  margin-bottom: 10px;
  font-weight: 300;
  font-size: 12px;
  color: ${colors.black};

  &::after {
    content: ':';
  }
`;
