import React from 'react';
import { Tab, Tabs as TabElem, TabList, TabPanel } from 'react-tabs';

import { TabsProps } from './props';
import { Container } from './styles';

const Tabs: React.FC<TabsProps> = ({
  tabOpened = 0,
  labels,
  tabs,
  onSelect = () => true,
}) => {
  return (
    <Container>
      <TabElem defaultIndex={tabOpened} onSelect={onSelect}>
        <TabList>
          {labels.map(elem => (
            <Tab
              key={elem.key}
              disabled={elem.disabled}
              disabledClassName="disabled"
              selectedClassName="active"
            >
              {elem.label}
            </Tab>
          ))}
        </TabList>

        {tabs.map(elem => (
          <TabPanel key={elem.key}>{elem.tab}</TabPanel>
        ))}
      </TabElem>
    </Container>
  );
};

export default Tabs;
