import React, { useCallback, useEffect, useState } from 'react';
import SelectElem from 'react-select';

import { SelectProps } from './props';
import { Container, Label } from './styles';
import CustomStyles from './customStyles';

const Select: React.FC<SelectProps> = ({
  id,
  name,
  value,
  options,
  label,
  disabled = false,
  searchable = true,
  loading = false,
  multiple = false,
  placeholder = 'Selecione',
  noOptionsMessage = 'Nenhuma opção disponível',
  focus = false,
  openMenuOnFocus = true,
  hideSelectedOptions = true,
  onchange,
}) => {
  const [selectValue, setselectValue] = useState<object | object[]>();
  const [selectFocused, setselectFocused] = useState(focus);

  useEffect(() => {
    if (Array.isArray(value)) {
      const selectedOptions = options.filter(
        option => value.indexOf(option.value) > -1,
      );

      setselectValue(selectedOptions);
    } else {
      const selectedOption = options.find(option => option.value === value);

      if (selectedOption) {
        setselectValue(selectedOption);
      }
    }
  }, [value, options]);

  const handleChange = useCallback(
    option => {
      if (Array.isArray(option)) {
        const selectedOptions = option.map(
          (selectedOption: any) => selectedOption.value,
        );

        onchange(selectedOptions);
      } else {
        onchange(option && option.value);
      }

      setselectValue(option);
    },
    [onchange],
  );

  return (
    <Container>
      {label && (
        <Label htmlFor={id} onClick={() => setselectFocused(state => !state)}>
          {label}
        </Label>
      )}

      <SelectElem
        inputId={id}
        name={name}
        value={selectValue}
        options={options}
        isDisabled={disabled}
        isSearchable={searchable}
        isLoading={loading}
        isMulti={multiple}
        autoFocus={selectFocused}
        openMenuOnFocus={openMenuOnFocus}
        isClearable
        onChange={option => handleChange(option)}
        noOptionsMessage={() => noOptionsMessage}
        hideSelectedOptions={hideSelectedOptions}
        placeholder={placeholder}
        styles={CustomStyles}
        menuPosition="absolute"
      />
    </Container>
  );
};

export default Select;
