import React from 'react';

import { TextAreaProps } from './props';
import { Container, Label, TextArea as TextAreaElem } from './styles';

const TextArea: React.FC<TextAreaProps> = ({ name, id, label, ...res }) => {
  return (
    <Container>
      {label && <Label>{label}</Label>}
      <TextAreaElem id={id} name={name} {...res} />
    </Container>
  );
};

export default TextArea;
