import React from 'react';

import Modal from 'components/Modal';
import Form from 'components/Form';
import Field from 'components/Form/Field';
import TextArea from 'components/TextArea';
import Button from 'components/Button';

import { ReactComponent as EditIcon } from 'assets/svg/edit-icon.svg';
import { ReactComponent as DeleteIcon } from 'assets/svg/delete-icon.svg';

import {
  Title,
  DateContainer,
  DayOfWeek,
  Date as DateText,
  EditButton,
  ButtonsContainer,
  DeleteButton,
  Details,
  Description,
  Data,
  ContainerRadios,
} from './styles';

interface AppointmentDetailsProps {
  isShowing: boolean;
  toggle(): void;
}

const AppointmentDetail: React.FC<AppointmentDetailsProps> = ({
  isShowing,
  toggle,
}) => {
  const modalTitle = () => {
    return (
      <Title>
        <DateContainer>
          <DayOfWeek>Terça-feira</DayOfWeek>
          <DateText>25 de novembro 2020</DateText>
        </DateContainer>

        <EditButton>
          <EditIcon />
          Editar
        </EditButton>
      </Title>
    );
  };

  return (
    <Modal
      isShowing={isShowing}
      hide={toggle}
      title={modalTitle()}
      hideCloseButton
      hideOkButton
    >
      <Details>
        <Description>Profissional</Description>
        <Data>Daine Santos</Data>

        <Description>Paciente</Description>
        <Data>Daine Souza</Data>

        <Description>Contatos</Description>
        <Data>(90) 91234-5678 - daine@gmail.com</Data>
      </Details>

      <Form>
        <ContainerRadios>
          <Field
            name="radio"
            id="radio"
            type="radio"
            label="Cliente cancelou"
            contrastBgColor="green"
          />

          <Field
            name="radio"
            id="radio2"
            type="radio"
            label="Cliente confirmou"
            contrastBgColor="violet"
          />
        </ContainerRadios>

        <TextArea id="textarea" name="textarea" placeholder="Observções" />

        <ButtonsContainer>
          <DeleteButton>
            <DeleteIcon />
            Excluir esse atendimento
          </DeleteButton>

          <Button text="Salvar" />
        </ButtonsContainer>
      </Form>
    </Modal>
  );
};

export default AppointmentDetail;
