import styled from 'styled-components';
import colors from '../../utils/styles/colors';
import { centerMiddle } from '../../utils/styles/commonStyles';

export const Container = styled.div`
  position: relative;

  ul {
    position: sticky;
    top: 0;
    display: flex;
    list-style: none;
    width: 100%;
    height: 46px;
    background-color: #f9f9f9;

    li {
      position: relative;
      ${centerMiddle}
      height: 46px;
      padding: 0 30px;
      color: ${colors.black_alt};
      text-transform: uppercase;
      text-decoration: none;
      font-size: 14px;
      cursor: pointer;
      outline: none;

      &:hover {
        background-color: ${colors.light_grey};
      }

      &.disabled {
        opacity: 0.5;
        background-color: #f9f9f9;
      }

      &.active {
        font-weight: 600;
        background-color: ${colors.white};
      }
    }
  }
`;
