import styled, { css } from 'styled-components';
import colors from '../../../../../../../../utils/styles/colors';
import { centerMiddle } from '../../../../../../../../utils/styles/commonStyles';

const buttonStyle = css`
  ${centerMiddle}
  border: 0;
  background: none;
  font-size: 13px;
  font-weight: 400;
  outline-color: ${colors.primary};

  svg {
    margin-right: 8px;
  }
`;

export const Title = styled.div`
  display: flex;
  justify-content: space-between;
  margin-right: 16px;
`;

export const DateContainer = styled.div`
  font-size: 16px;
`;

export const DayOfWeek = styled.div`
  text-transform: uppercase;
`;

export const Date = styled.strong`
  text-transform: uppercase;
`;

export const EditButton = styled.button`
  ${buttonStyle}
  color: ${colors.yellow};
`;

export const ButtonsContainer = styled.div`
  display: flex;
  justify-content: space-between;
`;

export const DeleteButton = styled.button`
  ${buttonStyle}
  color: ${colors.red};
`;

export const Details = styled.dl`
  display: grid;
  grid-column-gap: 8px;
  grid-template-columns: max-content auto;
  margin: 24px 0 0;
  font-size: 13px;
`;

export const Description = styled.dt`
  color: ${colors.dark_grey};
  text-align: right;

  &::after {
    content: ':';
  }

  & + dd {
    margin-bottom: 12px;
  }
`;

export const Data = styled.dd``;

export const ContainerRadios = styled.div`
  display: flex;
  justify-content: space-between;
  margin-bottom: 16px;

  div {
    flex: 1;
  }

  div + div {
    margin-left: 16px;
  }
`;
