/* eslint-disable no-nested-ternary */
import React, { useEffect } from 'react';

import useModal from 'hooks/modal';
import { showMessage } from 'utils/message';

import Calendar from 'components/Calendar';
import Tabs from 'components/Tabs';
import Select from 'components/Select';
import Button from 'components/Button';
import SideBar from 'components/SideBar';

import Schedule from 'components/Schedule';
import { useDispatch, useSelector } from 'react-redux';
import { IState } from 'store';
import EmptyView from 'components/ViewMessages/emptyview';

import LoadView from 'components/ViewMessages/loadView';
import AppointmentDetail from '../components/AppointmentDetail';
import AppointmentForm from '../components/AppointmentForm';

import { TabContainer, SelectContainer } from '../components/styles';
import { IAdminSchedulesState } from '../../../states/schedules/types';
import { getAdminSchedulesRequest } from '../../../states/schedules/actions';

const Scheduling: React.FC = () => {
  const { resulted, loading } = useSelector<IState, IAdminSchedulesState>(
    state => {
      return state.adminSchedules;
    },
  );
  const dispatch = useDispatch();

  useEffect(() => {
    if (!loading) {
      dispatch(getAdminSchedulesRequest({ date: new Date(2020, 0, 1, 8, 15) }));
    }
  }, [resulted]);

  const {
    isShowing: appointmentFormVisible,
    toggle: toggleAppointmentForm,
  } = useModal();

  const {
    isShowing: appointmentDetailVisible,
    toggle: toggleAppointmentDetail,
  } = useModal();
  return (
    <>
      <SideBar>
        <Calendar />

        <SelectContainer>
          <Select
            id="unidade"
            name="unidade"
            label="Selecione a Unidade"
            options={[
              { label: 'Unidade um', value: 1 },
              { label: 'Unidade dois', value: 2 },
              { label: 'Unidade três', value: 3 },
            ]}
            onchange={option => console.log(option)}
          />
        </SelectContainer>
      </SideBar>

      <TabContainer>
        <Tabs
          labels={[
            { label: 'Profissionais', key: 'Label1' },
            { label: 'Salas', key: 'Label2' },
          ]}
          tabs={[
            {
              key: 'Tab1',
              tab: (
                <div style={{ padding: '32px' }}>
                  <Button text="Abrir form" onClick={toggleAppointmentForm} />
                  <Button
                    text="Abrir detalhes"
                    onClick={toggleAppointmentDetail}
                  />

                  <Button
                    text="Exibir mensagem"
                    onClick={() =>
                      showMessage({
                        type: 'success',
                        content: 'Uma messagem de sucesso sendo exibida...',
                      })
                    }
                  />

                  <AppointmentForm
                    isShowing={appointmentFormVisible}
                    toggle={toggleAppointmentForm}
                  />

                  <AppointmentDetail
                    isShowing={appointmentDetailVisible}
                    toggle={toggleAppointmentDetail}
                  />

                  <br />
                  <br />
                  <br />
                  {loading ? (
                    <LoadView />
                  ) : (resulted?.success || []).length <= 0 ? (
                    <EmptyView myMessage="sem agendamento" />
                  ) : (
                    <Schedule data={resulted?.success || []} />
                  )}
                </div>
              ),
            },
            { key: 'Tab2', tab: <h1 key="Tab21">Salas</h1> },
          ]}
        />
      </TabContainer>
    </>
  );
};

export default Scheduling;
