import React, { useEffect, useMemo, useState } from 'react';
import { addHours, format, differenceInMinutes } from 'date-fns';

import { hoursToDecimal } from 'utils/time';

import {
  Container,
  Labels,
  Label,
  Hours,
  Hour,
  Data,
  Appointments,
  AppointmentsLine,
  AppointmentHour,
  Appointment,
  AppointmentTitle,
  AppointmentDescription,
} from './styles';

interface Hour {
  startHour: string;
  endHour: string;
}

interface Schedule {
  scheduleId: number;
  startDate: Date;
  endtDate: Date;
  title: string;
  description: string;
  startTime?: number;
  duration?: number;
  startHour?: string;
  endHour?: string;
}

interface Data {
  id: number;
  title: string;
  description: string;
  schedules: Schedule[];
}

interface ScheduleProps {
  data: Data[];
}

const Schedule: React.FC<ScheduleProps> = ({ data: originalData }) => {
  const [data, setData] = useState<Data[]>([]);
  const [labels, setLabels] = useState<
    { label: string; id: number; description: string }[]
  >([]);

  const hours = useMemo(() => {
    const today = new Date();
    const day = new Date(
      today.getFullYear(),
      today.getMonth(),
      today.getDay(),
      0,
    );

    const hoursFormatted: Hour[] = [];

    for (let index = 0; index < 24; index += 1) {
      hoursFormatted.push({
        startHour: format(addHours(day, index), 'HH:mm'),
        endHour: format(addHours(addHours(day, index), 1), 'HH:mm'),
      });
    }

    return hoursFormatted;
  }, []);

  useEffect(() => {
    document.getElementById('09:00-10:00')?.scrollIntoView({
      behavior: 'auto',
      block: 'center',
      inline: 'center',
    });
  }, []);

  useEffect(() => {
    const dataLabels = originalData.map(item => ({
      label: item.title,
      id: item.id,
      description: item.description,
    }));

    const formattedData = originalData.map(item => ({
      ...item,
      schedules: item.schedules.map(schedule => ({
        ...schedule,
        startTime:
          (hoursToDecimal(
            Number(format(new Date(schedule.startDate), 'HHmm')),
          ) *
            120) /
          100,
        duration:
          (differenceInMinutes(
            new Date(schedule.endtDate),
            new Date(schedule.startDate),
          ) /
            60) *
          120,
        startHour: format(new Date(schedule.startDate), 'HH:mm'),
        endHour: format(new Date(schedule.endtDate), 'HH:mm'),
      })),
    }));

    setLabels(dataLabels);
    setData(formattedData);
  }, [originalData]);

  const handleClikcHour = (startHour: string, endHour: string) => {
    console.log(`Agendamento de ${startHour} a ${endHour}`);
  };

  const handleClikApppointment = (itemData: Data, schedule: Schedule) => {
    console.log(itemData, schedule);
  };

  return (
    <Container>
      <Hours>
        {hours.map(hour => (
          <Hour
            key={`${hour.startHour}-${hour.endHour}`}
            id={`${hour.startHour}-${hour.endHour}`}
          >
            {hour.startHour}
          </Hour>
        ))}
      </Hours>

      <Data>
        <Labels>
          {labels.map(label => (
            <Label key={label.id}>
              {label.label}
              <div>{label.description}</div>
            </Label>
          ))}
        </Labels>

        <Appointments>
          {data.map(item => (
            <AppointmentsLine key={item.id}>
              {hours.map(hour => (
                <AppointmentHour
                  key={`${hour.startHour}-${hour.endHour}_${item.id}`}
                  onClick={() => handleClikcHour(hour.startHour, hour.endHour)}
                  role="button"
                  aria-pressed="false"
                />
              ))}

              {item.schedules.map(schedule => (
                <Appointment
                  key={`${schedule.startHour}-${schedule.endHour}_${schedule.scheduleId}`}
                  startHour={schedule.startTime || 0}
                  duration={schedule.duration || 0}
                  color="#FFFCEA"
                  role="button"
                  aria-pressed="false"
                  onClick={() => handleClikApppointment(item, schedule)}
                >
                  <AppointmentTitle>{`${schedule.title} - ${schedule.startHour}/${schedule.endHour}`}</AppointmentTitle>
                  <AppointmentDescription>
                    {schedule.description}
                  </AppointmentDescription>
                </Appointment>
              ))}
            </AppointmentsLine>
          ))}
        </Appointments>
      </Data>
    </Container>
  );
};

export default Schedule;
