import styled, { css } from 'styled-components';
import { transparentize } from 'polished';

import colors from 'utils/styles/colors';

import SquareIcon from 'assets/svg/square.svg';
import CircleIcon from 'assets/svg/circle.svg';

interface AppearanceProps {
  appearance?: 'border' | 'outline';
  inputType?: string;
  contrastBgColor?: string;
  visible?: boolean;
}

interface InputProps extends AppearanceProps {
  isFocused: boolean;
  isFillied: boolean;
}

export const Container = styled.div<AppearanceProps>`
  position: relative;
  display: ${props => (props.visible ? 'flex' : 'none')};

  & + div {
    margin-top: 80px;
  }

  ${({ appearance, inputType, contrastBgColor }) =>
    appearance === 'border' &&
    css`
      flex-direction: ${inputType === 'checkbox' || inputType === 'radio'
        ? 'row'
        : 'column-reverse'};
      margin-top: 16px;

      ${(inputType === 'checkbox' || inputType === 'radio') &&
      css`
        padding: 9px 8px 0;
        border: 1px solid ${transparentize(0.75, contrastBgColor || '')};
        background: ${transparentize(0.9, contrastBgColor || '')};
      `}

      & + div {
        margin-top: 16px;
      }
    `}
`;

export const Label = styled.label<AppearanceProps>`
  color: ${colors.black};

  ${({ appearance, inputType }) =>
    appearance === 'border'
      ? css`
          font-size: 12px;
          display: block;
          margin-bottom: 10px;
          font-weight: 300;

          &::after {
            content: ':';
          }

          ${(inputType === 'checkbox' || inputType === 'radio') &&
          css`
            font-weight: 400;
            font-size: 13px;
            margin: 2px 0 8px 32px;
            cursor: pointer;

            &::after {
              content: '';
            }
          `}
        `
      : css`
          color: ${colors.dark_grey};
          position: absolute;
          bottom: -32px;
          font-size: 15px;
          background: #fff;
          width: 100%;
          padding-top: 4px;
          cursor: text;
          transition: all 0.2s;
        `}
`;

export const Input = styled.input<InputProps>`
  outline: none;
  color: ${colors.black};

  &:focus {
    border-color: ${colors.primary};
  }

  ${({ appearance, isFocused, isFillied, type }) =>
    appearance === 'border'
      ? css`
          padding: 9px 14px;
          border-radius: 22px;
          border: 1px solid ${colors.light_grey};
          font-size: 12px;

          &:disabled {
            background-color: ${colors.light_grey};
            border-color: ${colors.dark_grey};
          }

          ${(type === 'checkbox' || type === 'radio') &&
          css`
            width: 0;

            &:checked::after,
            &::after {
              content: '';
              display: block;
              width: 14px;
              height: 14px;
              border: 1px solid ${colors.primary};
              border-radius: ${type === 'checkbox' ? '2px' : '50%'};
              margin: 1px 0 0 8px;
              cursor: pointer;
            }

            &::after {
              background-color: #fff;
            }

            &:checked::after {
              background: ${type === 'checkbox'
                ? `url(${SquareIcon}) `
                : `url(${CircleIcon})`};
              background-repeat: no-repeat;
              background-position: center center;
              background-size: 10px;
              background-color: #fff;
            }
          `}
        `
      : css`
          position: absolute;
          width: 100%;
          border: none;
          border-bottom: 1px solid ${colors.light_grey};
          padding: 11px 0;
          font-size: 15px;

          &:disabled {
            background: none;
            border-color: ${colors.dark_grey};
          }

          ${(isFocused || isFillied) &&
          css`
            & + label {
              font-size: 12px;
              bottom: -4px;
            }
          `}
        `};
`;

export const ErrorMessage = styled.span<AppearanceProps>`
  color: ${colors.red};
  font-size: 13px;

  ${({ appearance }) =>
    appearance === 'outline'
      ? css`
          position: absolute;
          bottom: -60px;
        `
      : css`
          margin-top: 4px;
        `}
`;
