/* eslint-disable */
import colors from '../../utils/styles/colors';

export default {
  option: (provided: any, state: any) => ({
    ...provided,
    border: '0',
    color: state.isSelected
      ? `${colors.white} !important`
      : `${colors.black} !important`,
    background: state.isSelected
      ? `${colors.primary} !important`
      : `${colors.white} !important`,
    fontSize: '12px',
    padding: '8px 16px',
    marginTop: '-1px',
  }),

  control: (provided: any, state: any) => {
    const borderRadius = state.isFocused ? '20px 20px 0 0' : '20px';
    const height = '34.4px';
    const minHeight = 'initial';
    const padding = state.isFocused ? '0 8px' : '0 8px';
    const fontSize = '12px';
    const border = state.isFocused
      ? `1px solid ${colors.primary} !important`
      : `1px solid ${colors.light_grey} !important`;
    const boxShadow = state.isFocused ? 'none' : 'none';

    return {
      ...provided,
      borderRadius,
      padding,
      fontSize,
      border,
      boxShadow,
      height,
      minHeight
    };
  },

  menu: () => ({
    padding: 0,
    background: 'none',
  }),

  menuList: (provided: any, state: any) => {
    const border = `1px solid ${colors.primary}`;
    const borderTop = `0`;
    const borderRadius = '0 0 20px 20px';

    return {
      ...provided,
      border,
      borderTop,
      borderRadius,
      padding: 0,
      background: '#fff',
      marginTop: '-2px',
    };
  },

  menuPortal: (provided: any, state: any) => ({ left: 0 }),

  indicatorsContainer: (provided: any, state: any) => (
    {...provided, marginTop: '-1px'}
  ),
};
