import React, { useState, useRef } from 'react';

import { InputProps } from './props';
import { Container, Input as InputElem, Label, ErrorMessage } from './styles';

export const Input: React.FC<InputProps> = ({
  name,
  id,
  type = 'text',
  label,
  value,
  autoComplete = 'off',
  placeholder,
  appearance = 'border',
  contrastBgColor = 'transparent',
  errorMessage,
  visible = true,
  onChange,
  ...rest
}) => {
  const inputRef = useRef<HTMLInputElement>(null);
  const [isFocused, setIsFocused] = useState(false);
  const [isFillied, setIsFillied] = useState(false);

  const handleInputFocus = () => {
    setIsFocused(true);
  };

  const handleInputBlur = () => {
    setIsFocused(false);
    setIsFillied(!!inputRef.current?.value);
  };

  return (
    <Container
      appearance={appearance}
      inputType={type}
      contrastBgColor={contrastBgColor}
      visible={visible}
    >
      <ErrorMessage appearance={appearance}>
        {errorMessage && <>{errorMessage}</>}
      </ErrorMessage>
      <InputElem
        ref={inputRef}
        {...rest}
        id={id}
        name={name}
        value={value}
        checked={!!value}
        type={type}
        placeholder={placeholder}
        onBlur={handleInputBlur}
        onFocus={handleInputFocus}
        onChange={onChange}
        isFocused={isFocused}
        isFillied={isFillied}
        autoComplete={autoComplete}
        appearance={appearance}
      />

      <Label htmlFor={id} appearance={appearance} inputType={type}>
        {label}
      </Label>
    </Container>
  );
};

export default Input;
