import React, { InputHTMLAttributes } from 'react';
import { useField } from 'formik';

import Input from 'components/Input';

interface InputProps extends InputHTMLAttributes<HTMLInputElement> {
  name: string;
  id: string;
  label?: string;
  errorMessage?: string;
  appearance?: 'border' | 'outline';
  contrastBgColor?: string;
  visible?: boolean;
}

export const Field: React.FC<InputProps> = ({
  name,
  id,
  value,
  type = 'text',
  label,
  autoComplete = 'off',
  placeholder,
  appearance = 'border',
  contrastBgColor = 'transparent',
  visible = true,
  ...rest
}) => {
  const [field, meta] = useField({ name, id, value });

  return (
    <Input
      {...field}
      id={id}
      name={name}
      value={field.value}
      type={type}
      placeholder={placeholder}
      autoComplete={autoComplete}
      appearance={appearance}
      contrastBgColor={contrastBgColor}
      label={label}
      errorMessage={meta.error}
      visible={visible}
      {...rest}
    />
  );
};

export default Field;
